.wrap{
	position: absolute;
	top: 0;
	left: 0;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 1.88rem;
	z-index: 1000000;
	
	&_open{
		position: fixed;
		left: 50%;
		transform: translate(-50%) translateZ(12px);
		background: var(--main);
	}
	&_white{
		color: var(--white);
		border: none;
	}
	&_border{
		border-bottom: 1px solid var(--black);
	}
	&_landing{
		height:3.7rem;
		align-items: flex-end;
		pointer-events: none;
		.title{
			width: 21.5rem;
			color: var( --green-light);
			font-size: 1.25rem;
			line-height: 1.25rem;
		}
	}
	
}

.title{
	font-weight: 300;
}
.menu{
	font-weight: 300;
}
.login{
	position: fixed;
	top: 1.25rem;
	right: 0.63rem;
	display: flex;
	align-items: center;
	justify-content: center;
	text-decoration: none;
	font-weight: 300;
	width: 5.06rem;
	height: 2.19rem;
	border: 0.06rem solid var(--new-orange);
	color: var(--new-orange);
	border-radius: 1.88rem;
	background: var(--white);
	z-index: 100;
}
.popup{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: calc(var(--vhd, 1vh) * 100);
	z-index: 10000;
	background: var(--greeny);
	transform:  translateZ(11px);
	&__inner{
		position: relative;
		padding-top: 2.8rem;
		padding-bottom: 1.38rem;
		display: flex;
		flex-direction: column;
		width: 100%;
		overflow: scroll;
		height: calc(var(--vhd, 1vh) * 100);
		-ms-overflow-style: none;
		scrollbar-width: none;
		&::-webkit-scrollbar {
			display: none;
		}
	}
}
.lk{
	display: flex;
	align-items: center;
	justify-content: center;
	height: 5.00rem;
	width: 100%;
	background: rgba(255, 255, 255, 0.15);
	border-radius: 0.5rem;
	color: var(--white);
	text-decoration: none;
	font-size: 1.38rem;
	line-height: 1.56rem;
}
.info{
	padding-bottom: 2rem;
	// margin: auto 0;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	&__label{
		padding: 0 0.75rem;
		height: 1.50rem;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 0.75rem;
		line-height: 1.25rem;
		letter-spacing: 0.07em;
		text-transform: uppercase;
		font-weight: 400;
		border-radius: 0.5rem;
		background: var(--yellow);
	}
	&__title{
		margin-top: 0.44rem;
		font-size: 1.75rem;
		line-height: 1.88rem;
		font-weight: 300;
		color: var(--white);
	}
}
.desc{
	margin-top: auto;
	
	color: var(--white);
	text-decoration: none;
	font-size: 1.38rem;
	line-height: 1.56rem;
}
.list{
	margin-top: 0.54rem;
	padding: 0.3rem 0.63rem;
	border-radius: 1.25rem;
	background: rgba(255, 255, 255, 0.1);
	&_disable{
		.link{
			&:nth-child(4), &:nth-child(5), &:nth-child(6){
				pointer-events: none;
				filter: blur(0.15rem);
				opacity: 0.2;
				*{
					
					pointer-events: none;
				}
			}
		}
		
	}
}
.pay{
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	top: 32.25rem;
	left: 50%;
	transform: translate(-50%);
	width: 20.94rem;
	&__text{
		text-align: center;
		color: var(--white);
		font-size: 1.38rem;
		line-height: 1.56rem;
		font-weight: 300;
	}
	&__btn{
		margin-top: 0.88rem;
		width: 10.25rem;
		height: 3.13rem;
		display: flex;

		align-items: center;
		justify-content: center;
		font-size: 0.75rem;
		line-height: 12.5rem;
		letter-spacing: 0.07rem;
		text-transform: uppercase;
		font-weight: 400;
		border-radius: 0.75rem;
		background: var(--yellow);
		cursor: pointer;
	}
}
.link{
	padding: 0.88rem 0 1.78rem;
	display: flex;
	flex-direction: column;
	min-height: 5.88rem;
	color: var(--white);
	text-decoration: none;
	border-bottom: 1px solid var(--greeny);
	&__desc{
		margin-bottom: 0.31rem;
		width: 100%;
	}
	&__text{
		width: 100%;
		font-size: 1.38rem;
		line-height: 1.56rem;
		// white-space: nowrap;
	}
	&_active{
		span:nth-child(2){
			display: flex;
			// align-items: center;
			&::before{
				display: block;
				width: 1rem;
				margin-right: 0.3rem;
				flex-shrink: 0;
				height: 1.56rem;
				content: '';
				background: url("data:image/svg+xml,%3Csvg width='17' height='12' viewBox='0 0 17 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.516 11.066C10.6773 9.922 11.0587 8.91733 11.66 8.052C12.276 7.172 13.0093 6.6 13.86 6.336H0V5.06H13.86C13.0093 4.796 12.276 4.23133 11.66 3.366C11.0587 2.486 10.6773 1.474 10.516 0.33L11.99 0C12.078 1.52533 12.54 2.72067 13.376 3.586C14.212 4.45133 15.2533 4.94267 16.5 5.06V6.336C15.2533 6.45333 14.212 6.94467 13.376 7.81C12.54 8.67533 12.078 9.87067 11.99 11.396L10.516 11.066Z' fill='white'/%3E%3C/svg%3E%0A");
				background-position: 50% 50%;
				background-size: contain;
				background-repeat: no-repeat;
			}
		}
	}
	&:last-child{
		border: none;
	}
}
@media screen and ( min-width: 641px) {
    .wrap{
        margin: 0 auto;
        width: 23.44rem;
		left: 50%;
		transform: translate(-50%);
			
		&_landing{
			width: 77.50rem;
			padding: 0;
			height: 2.8rem;
			.title{
				width: 100%;
				color: var( --black);
			}
		}
    }
	.title{
		cursor: pointer;
		&_disable{
			cursor: auto;
		}
	}
	.menu, .login{
		cursor: pointer;
	}
	.login{
		padding-bottom: 0.1rem;
		width: 5.44rem;
		height: 2.25rem;
		font-size: 1.25rem;
		right: 6.31rem;
		transition: all ease 0.3s;
		&:hover{
			color: var(--white);
			background: var(--new-orange);
		}
	}
}