.arrow {
    margin: 1.9rem auto 1.6rem;
    width: 1rem;
}

.border {
    margin: 4.7rem auto 0;
    width: 9.50rem;
    background: var(--white);
    height: 0.31rem;
    border-radius: 0.16rem;
}

.cards {
    margin-top: 1.9rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    &_1 {
        flex-direction: column;
        .card {
            margin-bottom: 0.64em;
            width: 100%;
            height: 25.875rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.card {
    padding: 0.9375rem;
    margin-bottom: 1.87rem;
    width: calc(50% - 0.31rem);
    height: 14.32rem;
    background: rgba(120, 166, 113, 0.10);
    border-radius: 1.25rem;
    &_wrong {
        background: rgba(249, 101, 29, 0.10);
    }
    &__emoji {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 1.6875rem;
        height: 1.7rem;
        width: 1.4rem;
    }
    &__text {
        margin-top: 0.61rem;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 1.6875rem;
        span {
            display: flex;
            font-weight: 400;
        }
    }
}

.container {
    position: relative;
    width: 100%;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
}

.cover {
    width: 100%;
    &__wrap {
        width: 100%;
    }
    &__inner {
        position: relative;
        padding-top: 6.63rem;
        display: flex;
        flex-direction: column;
    }
    &__text {
        margin-top: 1.8rem;
        width: 100%;
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 400;
        color: var(--new-orange);
    }
    &__img {
        // padding-left: 4.3rem;
        margin: 3.2rem -0.4rem 0;
        width: 23.03rem;
        overflow: hidden;
        transition: opacity ease 0.3s;
    }
    &__desc {
        margin-top: 3.9rem;
        width: 100%;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 1.6875rem;
        img {
            display: none;
        }
    }
    &__type {
        // margin-top: -1rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        font-weight: 400;
    }
    &__title {
        font-size: 2.625rem;
        font-weight: 400;
        line-height: 2.75rem;
        /* 104.762% */
        letter-spacing: -0.02625rem;
    }
    &__btns {
        display: none;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 3.50rem;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        width: 100%;
        text-align: center;
        font-size: 1rem;
        line-height: 1.5rem;
        svg {
            display: none;
        }
    }
}

.img {
    margin-top: 5.1rem;
    margin-bottom: 4.9rem;
    width: 100%;
    &_2 {
        margin-top: 2rem;
    }
    &_3 {
        margin-top: 2rem;
        margin-bottom: 1.7rem;
    }
}

.h2 {
    font-size: 2.5rem;
    font-style: normal;
    font-weight: 300;
    line-height: 3rem;
}

.fixedWrap {
    padding: 1.25rem 1.25rem 0.63rem;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 7.50rem;
    background: var(--almost-white);
    border-radius: 2.5rem 2.5rem 0 0;
    z-index: 1000;
    pointer-events: none;
    transform: translateY(120%) translateZ(10px);
    transition: transform ease 0.6s;
    span {
        width: 100%;
        color: var(--green-light);
        br {
            display: none;
        }
    }
    &_hide {
        transform: translateY(0%) translateZ(0);
        pointer-events: auto;
    }
}

.fixedBtn {
    // position: fixed;
    // bottom: 0;
    // left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 3.75rem;
    border-radius: 2.00rem;
    background: var(--yellow);
    text-decoration: none;
    font-weight: 300;
    font-size: 1rem;
    line-height: 1.25rem;
    // text-transform: uppercase;
    // letter-spacing: 0.07em;
    color: var(--black);
    &_buy {
        display: inline-flex;
        position: static;
        flex-direction: column;
        span {
            display: flex;
            white-space: nowrap;
        }
        // width: 10.25rem;
    }
    &_try {
        position: static;
        width: 10.25rem;
        line-height: 1rem;
        text-align: center;
        background: var(--green-light);
        color: var(--white);
    }
    &__desc {
        line-height: 1rem;
        font-size: 0.75rem;
    }
    &__price {
        margin-left: 0.2rem;
        display: flex;
        align-items: center;
        // text-transform: uppercase;
        // font-weight: 300;
        // letter-spacing: 0.07em;
        // font-size: 1rem;
        // line-height: 1.25rem;
    }
}

.footer {
    padding-bottom: 5rem;
    margin-top: 0.1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--green-dark);
    &__img {
        width: 13.13rem;
    }
    &__text {
        color: var(--greeny, #78A671);
        font-size: 1.625rem;
        font-weight: 300;
        line-height: 2rem;
    }
    &__title {
        margin-top: 0.63rem;
        width: 100%;
        max-width: 33.25rem;
        text-align: center;
        font-size: 2.50rem;
        line-height: 2.50rem;
        font-weight: 400;
    }
    &__btns {
        margin-top: 5rem;
        flex-direction: column;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4.375rem;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-weight: 300;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        width: 100%;
        text-align: center;
        font-size: 1rem;
        line-height: 1.5rem;
        svg {
            display: none;
        }
    }
    &__desc {
        margin-top: 4.9rem;
        width: 100%;
        max-width: 41.31rem;
        font-size: 1.25rem;
        line-height: 1.69rem;
        a {
            text-decoration: none;
            font-weight: 400;
        }
    }
}

.hidder {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    height: 48.15rem;
    width: 100%;
}

.label {
    margin: 0 auto 0.94rem;
    display: flex;
    padding: 0.25rem 0rem;
    width: 8.2rem;
    justify-content: center;
    align-items: center;
    border-radius: 0.625rem;
    background: var(--new-orange, #F9651D);
    color: var(--white);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.25rem;
    letter-spacing: 0.07rem;
    text-transform: uppercase;
    &_2 {
        margin: 1.88rem auto 0.94rem;
        width: 8.4rem;
    }
}

.oldPrice {
    margin-right: 0.63rem;
    position: relative;
    font-weight: 400;
    font-size: 0.75rem;
    color: var(--red);
    &:after {
        position: absolute;
        top: 0.65rem;
        left: -0.1rem;
        content: '';
        width: 2.19rem;
        height: 1px;
        background: var(--red);
        border-bottom: 1px solid var(--yellow);
        transform-origin: 50% 50%;
        transform: rotate(-18.5deg);
    }
}

.popup {
    position: fixed;
    padding: 1.25rem 0.63rem 0.63rem;
    min-height: 11.25rem;
    width: 22.19rem;
    left: 50%;
    transform: translate(-50%, 150%) translateZ(10px);
    bottom: 0.63rem;
    background: var(--white);
    transition: transform ease 0.5s;
    z-index: 1001;
    border-radius: 1.25rem;
    box-shadow: 0rem 0.13rem 1.56rem rgba(0, 0, 0, 0.1);
    &_active {
        transform: translate(-50%, 0%) translateZ(10px);
    }
    // &_submit{
    //     background: var(--main);
    //     color: var(--white);
    //     .popup__close{
    //         path{
    //             stroke: var(--white);
    //         }
    //     }
    // }
    &__close {
        position: absolute;
        top: 1rem;
        right: 1rem;
        z-index: 10;
        width: 0.75rem;
        height: 0.75rem;
        cursor: pointer;
    }
    &__header {
        margin-bottom: 0.25rem;
        display: flex;
        align-items: center;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        span {
            padding: 0.13rem 0.69rem;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-left: 0.5rem;
            background: var(--yellow);
            border-radius: 0.50rem;
        }
    }
    &__title {
        font-size: 1.38rem;
        line-height: 1.56rem;
        width: 19.25rem;
    }
    &__desc {
        margin-top: 0.5rem;
        width: 19.38rem;
    }
    &__price {
        margin-top: 3.88rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        text-align: center;
    }
    &__btns {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__btn {
        height: 2.5rem;
        width: 10.31rem;
        border-radius: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--main);
        color: var(--white);
        font-weight: 500;
        font-size: 1rem;
        cursor: pointer;
        &_disable {
            opacity: 0.2;
            pointer-events: none;
        }
    }
}

.pretext {
    margin: 6rem auto 3rem;
    width: 22.18rem;
}

.section {
    margin-top: 5rem;
    width: 100%;
    h2 {
        margin-bottom: 1.8rem;
    }
}

.stages {
    margin-top: 6.25rem;
    h2 {
        padding: 0 0.63rem;
        margin-bottom: 1.25rem;
    }
    &__text {
        font-size: 1.38rem;
        line-height: 1.75rem;
        padding: 0 0.63rem;
    }
}

.text {
    width: 100%;
    &_5 {
        p:nth-child(3) {
            width: 101%;
        }
    }
    &_7 {
        width: 101%;
    }
}

.btn {
    position: relative;
    margin: 4.08rem auto .65rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16.25rem;
    height: 13.75rem;
    color: var(--black);
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    z-index: 10;
    text-align: center;
    text-decoration: none;
    &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        background: var(--yellow);
        border-radius: 12.5rem;
        filter: blur(1.8rem);
        animation: btn-rotate 2s ease-in-out 0.3s infinite alternate;
        z-index: -1;
    }
    &__desc {
        position: absolute;
        text-align: center;
        left: 50%;
        transform: translate(-50%);
        bottom: 0.65rem;
        font-weight: 300;
        font-size: 1rem;
        line-height: 1.25rem;
    }
    &_buy {
        padding-bottom: 1.4rem;
        text-transform: unset;
        font-size: 1.38rem;
        line-height: 1.56rem;
        letter-spacing: normal;
        flex-direction: column;
        align-items: center;
        .oldPrice {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -3rem);
            font-size: 1.00rem;
            font-weight: 300;
            &::after {
                width: 2.91rem;
                top: 0.75rem;
                left: 0rem;
            }
        }
    }
}

.white_wrapper {
    padding-top: 1px;
    position: relative;
    background: var(--grey);
    color: var(--black);
}

.subscribe {
    margin-top: 9.2rem;
    padding-bottom: 6.36rem;
}

.rec {
    padding-bottom: 9.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 3.13rem auto 0;
    color: var(--green-dark);
    font-size: 1.25rem;
    line-height: 1.69rem;
    &__col {
        display: flex;
        flex-direction: column;
        align-items: center;
        &+.rec__col {
            margin-top: 1.88rem;
        }
    }
    span+span {
        margin-top: 0.36rem;
    }
    a {
        text-decoration: none;
        border-bottom: 0.07rem solid #828282;
        &+a {
            margin-top: 0.36rem;
        }
    }
}

.quote {
    margin-top: 4.1rem;
    width: 100%;
    font-size: 1.625rem;
    font-weight: 300;
    line-height: 2rem;
    color: var(--new-orange);
    &_2 {
        margin: 1.8rem 0;
    }
}

.wrap {
    padding-top: 2.9rem;
    border-radius: 2.5rem 2.5rem 0rem 0rem;
    width: 100%;
}

@keyframes btn-rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
        background: #F7F9A5;
    }
    100% {
        transform: translate(-50%, -50%) rotate(15deg);
        background: #FEF394;
    }
}

@keyframes button-loading-spinner {
    from {
        transform: rotate(0turn);
    }
    to {
        transform: rotate(1turn);
    }
}

@media screen and ( min-width: 641px) {
    .cover__inner {}
    .btn, .fixedBtn {
        cursor: pointer;
    }
    .card {
        margin-bottom: 1.88rem;
        height: auto;
        min-height: 9.25rem;
    }
    .cards {
        margin-top: 1.9rem;
        flex-direction: row;
        &_1 {
            .card {
                margin-bottom: 0;
                width: calc(50% - 0.31rem);
                height: 25.25rem;
            }
        }
    }
    .container {
        margin: 0 auto;
        position: relative;
        width: 77.50rem;
        padding: 0;
    }
    .cover {
        position: relative;
        &__inner {
            padding-top: 5.73rem;
        }
        &__title {
            width: 38.8rem;
            font-size: 4.5rem;
            font-weight: 400;
            line-height: 105%;
            letter-spacing: -0.045rem;
        }
        &__img {
            margin: 0;
            position: absolute;
            width: 38.8rem;
            right: 0rem;
            top: 0;
            height: 100%;
            overflow: unset;
            img {
                position: sticky;
                top: 6.58rem;
                left: 0;
            }
        }
        &__text {
            width: 38.8rem;
            margin-top: 2.9rem;
            width: 36.69rem;
        }
        &__desc {
            margin-top: 4rem;
            width: 38.8rem;
            font-size: 1.375rem;
            font-style: normal;
            font-weight: 300;
            line-height: 1.6875rem;
        }
        &__btns {
            margin-top: 1.58rem;
            display: flex;
        }
        &__btn {
            height: 4.69rem;
            width: 23.75rem;
            border-radius: 1.88rem;
            cursor: pointer;
            transition: all ease 0.3s;
            &:hover {
                background: var(--white);
                color: var(--new-orange);
            }
        }
        &__refund {
            margin-top: 0;
            margin-left: 1.25rem;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            svg {
                display: block;
                width: 1.25rem;
            }
            span {
                margin-left: 0.5rem;
            }
        }
    }
    .hide_desc {
        display: none !important;
    }
    .fixedWrap {
        display: none;
    }
    .img {
        margin-top: 5.8rem;
        margin-bottom: 5.6rem;
        &_2 {
            margin-top: 2rem;
        }
        &_3 {
            margin-top: 2rem;
            margin-bottom: 1.8rem;
        }
    }
    .section {
        margin: 5.63rem auto 0;
        width: 38.0625rem;
        .container {
            width: 100%;
        }
    }
    .pretext {
        margin: 9rem auto 9rem;
        width: 38.0625rem;
    }
    .review {
        margin: 9.48rem auto 0;
        padding: 3.75rem 3.13rem;
        width: 83.75rem;
        h2 {
            margin-bottom: 1.25rem;
        }
        &__text {
            width: 25rem;
            margin-bottom: 3.1rem;
        }
    }
    .rec {
        padding-bottom: 2.25rem;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 42.06rem;
        text-align: left;
        margin: 5.44rem auto 0;
        color: var(--green-dark);
        font-size: 1.25rem;
        line-height: 1.69rem;
        &__col {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            &+.rec__col {
                margin-top: 0rem;
            }
        }
    }
    .footer {
        margin-top: 0rem;
        &__btns {
            margin-top: 2.31rem;
            display: flex;
            align-items: center;
        }
        &__img {
            width: 13.13rem;
        }
        &__text {
            margin: 0 auto;
            width: 38.0625rem;
        }
        &__title {
            margin-top: 1.93rem;
        }
        &__btns {
            margin-top: 5.71rem;
        }
        &__btn {
            height: 4.375rem;
            width: 24.31rem;
            border-radius: 2.5rem;
            cursor: pointer;
            transition: all ease 0.3s;
            &:hover {
                background: var(--white);
                color: var(--new-orange);
            }
        }
        &__refund {
            margin-top: 0.94rem;
            margin-left: 0;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            text-align: center;
            svg {
                display: block;
                width: 1.25rem;
            }
            span {
                margin-left: 0.5rem;
            }
        }
        &__desc {
            margin: 5.7rem auto 0;
            width: 38.0625rem;
            font-size: 1.25rem;
            color: #828282;
            font-weight: 300;
            line-height: 1.6875rem;
        }
    }
    .quote {
        margin-top: 3.8rem;
        width: 38.8rem;
        font-size: 2.5rem;
        font-weight: 300;
        line-height: 3rem;
        &_2 {
            margin: 1.8rem 0;
            width: 38.0625rem;
            font-size: 1.625rem;
            font-weight: 300;
            line-height: 2rem;
        }
    }
    .wrap {
        padding-top: 5rem;
        border-radius: 0rem;
    }
}