.author {
    margin-top: 4.9rem;
    display: flex;
    flex-direction: column;
    padding: 1.00rem 0.38rem 0.38rem;
    border-radius: 2.5rem;
    color: var(--new-orange);
    border: 0.25rem solid var(--new-orange);
    &__info {
        width: 100%;
        width: 100%;
        padding: 0.63rem;
    }
    &__title {
        // margin-top: 1.6rem;
        font-size: 2.5rem;
        line-height: 3rem;
    }
    &__text {
        margin-top: 1.25rem;
        width: 100%;
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 300;
    }
    &__desc {
        margin-top: 1.25rem;
        display: flex;
        flex-direction: column;
        font-size: 2.5rem;
        line-height: 3rem;
    }
    &__img {
        margin-top: 1.25rem;
        width: 100%;
        img {
            border-radius: 1.88rem;
        }
    }
    &__list {
        margin-top: 2.1rem;
    }
    &__item {
        margin: 0.3rem -0.63rem 0;
        padding: 0.85rem 1.88rem;
        display: flex;
        flex-direction: column;
        width: 23.44rem;
        height: 4.86rem;
        background: var(--white);
        color: var(--black);
        border-radius: 3.13rem;
    }
    &__number {
        font-size: 1.75rem;
        line-height: 1.88rem;
    }
}

.border {
    margin: 4.7rem auto 0;
    width: 9.50rem;
    background: var(--white);
    height: 0.31rem;
    border-radius: 0.16rem;
}

.cards {
    margin-top: 2.6rem;
}

.container {
    position: relative;
    width: 100%;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
}

.cover {
    width: 100%;
    &__wrap {
        width: 100%;
    }
    &__inner {
        position: relative;
        padding-top: 6.13rem;
        display: flex;
        flex-direction: column;
    }
    &__text {
        margin-top: 1.8rem;
        width: 100%;
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 400;
        color: var(--new-orange);
    }
    &__img {
        // padding-left: 4.3rem;
        margin: 2.4rem -0.6rem 0;
        width: 22.93rem;
        opacity: 0;
        overflow: hidden;
        transition: opacity ease 0.3s;
    }
    &__desc {
        margin-top: 1.8rem;
        width: 100%;
        font-size: 1.38rem;
        line-height: 1.75rem;
        img {
            display: none;
        }
    }
    &__sticky {
        // margin-bottom: -3.7rem;
        // position: sticky;
        // left: 0;
        // bottom: 3.7rem;
    }
    &__type {
        // margin-top: -1rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        font-weight: 400;
    }
    &__title {
        width: 97%;
        font-size: 2.19rem;
        line-height: 2.5rem;
        letter-spacing: -0.01em;
        color: var(--green-dark);
        font-weight: 400;
    }
    &__btns {
        display: none;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 3.50rem;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        width: 100%;
        text-align: center;
        font-size: 1rem;
        line-height: 1.5rem;
        svg {
            display: none;
        }
    }
}

.for {
    margin-top: 6rem;
    h2 {
        // text-align: center;
        margin-bottom: 3.6rem;
    }
}

.form {
    margin-top: 1.38rem;
    display: flex;
    flex-direction: column;
    &__label {
        padding: 0.75rem 0.75rem 1.25rem;
        height: 5.63rem;
        display: flex;
        flex-direction: column;
        border-radius: 0.75rem;
        background: var(--grey);
    }
    &__desc {
        font-size: 0.75rem;
        letter-spacing: 0.07em;
        text-transform: uppercase;
    }
    &__input {
        margin-top: 0.5rem;
        padding: 0.31rem 0;
        height: 1.88rem;
        border: none;
        background: none;
        border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        outline: unset;
        font-weight: 300;
        user-select: unset;
        &_disable {
            border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        }
    }
    &__btn {
        margin-top: 0.63rem;
        margin-left: auto;
        width: 10.44rem;
        height: 2.50rem;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: unset;
        border-radius: 1.25rem;
        border: 0.13rem solid var(--main);
        background: var(--main);
        color: var(--white);
        letter-spacing: 0.07em;
        text-transform: uppercase;
        font-size: 0.75rem;
        transition: all ease 0.3s;
        cursor: pointer;
        &_disable {
            background: transparent;
            opacity: 0.2;
            color: var(--black);
            border: 0.13rem solid #BDBDBD;
            pointer-events: none;
        }
        &_loading {
            position: relative;
            color: transparent;
            pointer-events: none;
            user-select: none;
            &::after {
                content: "";
                position: absolute;
                width: 1.5rem;
                height: 1.5rem;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                border: 0.13rem solid transparent;
                border-top-color: #ffffff;
                border-radius: 50%;
                animation: button-loading-spinner 1s linear infinite;
            }
        }
    }
}

.fixedWrap {
    padding: 1.25rem 1.25rem 0.63rem;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 7.50rem;
    background: var(--almost-white);
    border-radius: 2.5rem 2.5rem 0 0;
    z-index: 1000;
    pointer-events: none;
    transform: translateY(120%) translateZ(10px);
    transition: transform ease 0.6s;
    span {
        width: 100%;
        color: var(--green-light);
        br {
            display: none;
        }
    }
    &_hide {
        transform: translateY(0%) translateZ(0);
        pointer-events: auto;
    }
}

.fixedBtn {
    // position: fixed;
    // bottom: 0;
    // left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 3.75rem;
    border-radius: 2.00rem;
    background: var(--yellow);
    text-decoration: none;
    font-weight: 300;
    font-size: 1rem;
    line-height: 1.25rem;
    // text-transform: uppercase;
    // letter-spacing: 0.07em;
    color: var(--black);
    &_buy {
        display: inline-flex;
        position: static;
        flex-direction: column;
        span {
            display: flex;
            white-space: nowrap;
        }
        // width: 10.25rem;
    }
    &_try {
        position: static;
        width: 10.25rem;
        line-height: 1rem;
        text-align: center;
        background: var(--green-light);
        color: var(--white);
    }
    &__desc {
        line-height: 1rem;
        font-size: 0.75rem;
    }
    &__price {
        margin-left: 0.2rem;
        display: flex;
        align-items: center;
        // text-transform: uppercase;
        // font-weight: 300;
        // letter-spacing: 0.07em;
        // font-size: 1rem;
        // line-height: 1.25rem;
    }
}

.faq {
    padding-top: 5.9rem;
    &__list {
        margin-top: 2rem;
    }
}

.footer {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #828282;
    &__img {
        width: 13.13rem;
    }
    &__title {
        font-size: 1.625rem;
        font-style: normal;
        font-weight: 300;
        line-height: 2rem;
        text-align: center;
        color: var(--new-orange);
    }
    &__subtitle {
        margin-top: 0.5em;
        text-align: center;
        font-size: 1rem;
        line-height: 1.25rem;
        font-weight: 300;
    }
    &__form {
        margin-top: 1.56rem;
    }
    &__btns {
        margin-top: 3rem;
        width: 17.5rem;
        height: 3.5rem;
        flex-direction: column;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 3.50rem;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        width: 100%;
        text-align: center;
        font-size: 1rem;
        line-height: 1.5rem;
        svg {
            display: none;
        }
    }
    &__desc {
        margin-top: 3rem;
        width: 100%;
        padding: 0 0.63rem;
        text-align: center;
        max-width: 41.31rem;
        font-size: 1.25rem;
        line-height: 1.69rem;
        a {
            text-decoration: none;
            font-weight: 400;
        }
    }
}

.hidder {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;
    height: 48.15rem;
    width: 100%;
}

.inside {
    padding: 0 0.63rem;
    h2 {
        margin-bottom: 5.94rem;
    }
}

.included {
    position: relative;
    margin-top: 7rem;
    h2 {
        margin-top: 5.7rem;
        margin-bottom: 2.5rem;
    }
}

.oldPrice {
    margin-right: 0.63rem;
    position: relative;
    font-weight: 400;
    font-size: 0.75rem;
    color: var(--red);
    &:after {
        position: absolute;
        top: 0.65rem;
        left: -0.1rem;
        content: '';
        width: 2.19rem;
        height: 1px;
        background: var(--red);
        border-bottom: 1px solid var(--yellow);
        transform-origin: 50% 50%;
        transform: rotate(-18.5deg);
    }
}

.popup {
    position: fixed;
    padding: 1.25rem 0.63rem 0.63rem;
    min-height: 11.25rem;
    width: 22.19rem;
    left: 50%;
    transform: translate(-50%, 150%) translateZ(10px);
    bottom: 0.63rem;
    background: var(--white);
    transition: transform ease 0.5s;
    z-index: 1001;
    border-radius: 1.25rem;
    box-shadow: 0rem 0.13rem 1.56rem rgba(0, 0, 0, 0.1);
    &_active {
        transform: translate(-50%, 0%) translateZ(10px);
    }
    // &_submit{
    //     background: var(--main);
    //     color: var(--white);
    //     .popup__close{
    //         path{
    //             stroke: var(--white);
    //         }
    //     }
    // }
    &__close {
        position: absolute;
        top: 1rem;
        right: 1rem;
        z-index: 10;
        width: 0.75rem;
        height: 0.75rem;
        cursor: pointer;
    }
    &__header {
        margin-bottom: 0.25rem;
        display: flex;
        align-items: center;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        span {
            padding: 0.13rem 0.69rem;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-left: 0.5rem;
            background: var(--yellow);
            border-radius: 0.50rem;
        }
    }
    &__title {
        font-size: 1.38rem;
        line-height: 1.56rem;
        width: 19.25rem;
    }
    &__desc {
        margin-top: 0.5rem;
        width: 19.38rem;
    }
    &__price {
        margin-top: 3.88rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        text-align: center;
    }
    &__btns {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__btn {
        height: 2.5rem;
        width: 10.31rem;
        border-radius: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--main);
        color: var(--white);
        font-weight: 500;
        font-size: 1rem;
        cursor: pointer;
        &_disable {
            opacity: 0.2;
            pointer-events: none;
        }
    }
}

.pretext {
    margin: 6rem auto 3rem;
    width: 22.18rem;
}

.stages {
    margin-top: 6.25rem;
    h2 {
        padding: 0 0.63rem;
        margin-bottom: 1.25rem;
    }
    &__text {
        font-size: 1.38rem;
        line-height: 1.75rem;
        padding: 0 0.63rem;
    }
}

.review {
    margin: 5.85rem auto 0;
    padding: 1.88rem 0.63rem;
    width: 22.19rem;
    background: var(--almost-white);
    border-radius: 2.5rem;
    h2 {
        margin-bottom: 1.4rem;
    }
    &__list {
        margin-top: 1.5rem;
    }
    &__item {
        padding: 1.25rem 0.63rem 3.13rem;
        background: var(--white);
        border-radius: 0.50rem;
        &+.review__item {
            margin-top: 0.73rem;
        }
    }
    &__header {
        display: flex;
        align-items: center;
    }
    &__img {
        margin-right: 0.94rem;
        width: 5.00rem;
        height: 5.00rem;
        border-radius: 50%;
        overflow: hidden;
    }
    &__name {
        font-size: 1.38rem;
        line-height: 1.56rem;
        font-weight: 400;
    }
    &__desc {
        font-size: 1rem;
        line-height: 1.25rem;
    }
    &__text {
        width: 20.50rem;
        font-size: 1.38rem;
        line-height: 1.75rem;
        margin-bottom: 1rem;
        span {
            font-weight: 500;
        }
        p+p {
            margin-top: 0.6rem;
        }
    }
}

.text {
    &_1 {
        margin-top: 0.2rem;
        p+p {
            margin-top: 0.6rem;
        }
    }
    &_2 {
        margin-top: 4.9rem;
        h2 {
            margin-bottom: 0.3rem;
        }
        p+p {
            margin-top: 0.6rem;
        }
    }
}

.help {
    position: relative;
    margin-top: 2.1rem;
    margin-bottom: -3.75rem;
    z-index: 10;
    &__list {
        margin-top: 1.9rem;
        font-size: 1.38rem;
        line-height: 1.56rem;
    }
    &__item {
        margin: 0.1rem -0.63rem 0;
        padding: 1.25rem;
        display: flex;
        align-items: center;
        height: 7.50rem;
        width: 23.44rem;
        border-radius: 3.75rem;
        background: var(--yellow);
        color: var(--black);
    }
    &__number {
        width: 0.81rem;
        font-size: 1.38rem;
    }
    &__text {
        margin-left: 1.69rem;
        width: 17.00rem;
        font-size: 1rem;
        line-height: 1.25rem;
    }
}

.case {
    margin-top: 5.1rem;
    margin-bottom: 0.63rem;
    padding: 1.88rem 0.63rem;
    height: 36.25rem;
    display: flex;
    flex-direction: column;
    border-radius: 1.25rem;
    background: rgba(255, 255, 255, 0.1);
    &__title {
        margin-bottom: 0.6rem;
        width: 19.94rem;
        font-size: 1.38rem;
        line-height: 1.56rem;
    }
    p+p {
        margin-top: 0.6rem;
    }
}

.btn {
    position: relative;
    margin: 4.08rem auto .65rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16.25rem;
    height: 13.75rem;
    color: var(--black);
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    z-index: 10;
    text-align: center;
    text-decoration: none;
    &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        background: var(--yellow);
        border-radius: 12.5rem;
        filter: blur(1.8rem);
        animation: btn-rotate 2s ease-in-out 0.3s infinite alternate;
        z-index: -1;
    }
    &__desc {
        position: absolute;
        text-align: center;
        left: 50%;
        transform: translate(-50%);
        bottom: 0.65rem;
        font-weight: 300;
        font-size: 1rem;
        line-height: 1.25rem;
    }
    &_buy {
        padding-bottom: 1.4rem;
        text-transform: unset;
        font-size: 1.38rem;
        line-height: 1.56rem;
        letter-spacing: normal;
        flex-direction: column;
        align-items: center;
        .oldPrice {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -3rem);
            font-size: 1.00rem;
            font-weight: 300;
            &::after {
                width: 2.91rem;
                top: 0.75rem;
                left: 0rem;
            }
        }
    }
}

.white_wrapper {
    padding-top: 1px;
    position: relative;
    background: var(--grey);
    color: var(--black);
}

.subscribe {
    margin-top: 9.2rem;
    padding-bottom: 6.36rem;
}

.rec {
    padding-bottom: 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 1.5rem auto 0;
    color: var(--green-dark);
    font-size: 1.25rem;
    line-height: 1.69rem;
    &__col {
        display: flex;
        flex-direction: column;
        align-items: center;
        &+.rec__col {
            margin-top: 1.5rem;
        }
    }
    span+span {
        margin-top: 0.36rem;
    }
    a {
        text-decoration: none;
        border-bottom: 0.07rem solid #828282;
        &+a {
            margin-top: 0.36rem;
        }
    }
}

.wrap {
    padding-top: 2.9rem;
    padding-bottom: 3rem;
    border-radius: 2.5rem;
    width: 100%;
    background: var(--new-bg);
    color: var(--green-dark);
}

@keyframes btn-rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
        background: #F7F9A5;
    }
    100% {
        transform: translate(-50%, -50%) rotate(15deg);
        background: #FEF394;
    }
}

@keyframes button-loading-spinner {
    from {
        transform: rotate(0turn);
    }
    to {
        transform: rotate(1turn);
    }
}

@media screen and ( min-width: 641px) {
    .cover__inner {}
    .btn, .fixedBtn {
        cursor: pointer;
    }
    .container {
        margin: 0 auto;
        position: relative;
        width: 77.50rem;
        padding: 0;
    }
    .cover {
        position: relative;
        &__inner {
            padding-top: 5.73rem;
