            }
        }
        &__refund {
            margin-top: 0;
            margin-left: 1.25rem;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            svg {
                display: block;
                width: 1.25rem;
            }
            span {
                margin-left: 0.5rem;
