    }
    .fixedWrap {
        display: none;
    }
    .inside {
        padding: 0;
    }
    .stages {
        margin-top: 8.75rem;
        &__text {
            padding: 0;
            width: 31.38rem;
            margin-bottom: 0.9rem;
        }
        h2 {
            padding: 0;
            margin-bottom: 1.25rem;
        }
    }
    .for {
        margin-top: 6.9rem;
        h2 {
            text-align: center;
            margin-bottom: 3.44rem;
        }
    }
    .pretext {
        margin: 9rem auto 9rem;
        width: 38.0625rem;
    }
    .review {
        margin: 9.48rem auto 0;
        padding: 3.75rem 3.13rem;
        width: 83.75rem;
        h2 {
            margin-bottom: 1.25rem;
        }
        &__text {
            width: 25rem;
            margin-bottom: 3.1rem;
        }
    }
    .author {
        position: relative;
        margin-top: 9.18rem;
        display: flex;
        flex-direction: column;
        padding: 0;
        border: none;
        height: 30.06rem;
        &__info {
            padding-left: 6.56rem;
            padding-top: 5.8rem;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            border-radius: 2.5rem;
            border: 0.25rem solid var(--new-orange);
        }
        &__title {
            width: 27.88rem;
            margin-bottom: 0;
        }
        &__text {
            width: 27.88rem;
        }
        &__desc {
            margin-top: 3.38rem;
            width: 31.88rem;
        }
        &__img {
            margin-top: 0;
            position: absolute;
            top: 50%;
            right: 6.56rem;
            height: calc(100% + 1px);
            width: auto;
            z-index: 100;
            transform: translateZ(0) translateY(-50%);
            img {
                height: 100%;
                width: auto;
                border-radius: 0;
            }
        }
    }
    .rec {
        padding-bottom: 9rem;
        align-items: center;
        width: 42.06rem;
        text-align: center;
        margin: 3rem auto 0;
        color: var(--green-dark);
        font-size: 1.25rem;
        line-height: 1.69rem;
        &__col {
            display: flex;
        }
    }
    .footer {
        margin-top: 9.4rem;
        &__btns {
            margin-top: 3rem;
            display: flex;
            align-items: center;
        }
        &__img {
            width: 13.13rem;
        }
        &__subtitle {
            margin-top: 0.5em;
            text-align: center;
            font-size: 1rem;
            line-height: 1.25rem;
            font-weight: 300;
        }
        &__form {
            margin-top: 3rem;
            width: 38.0625rem;
        }
        &__btn {
            width: 17.5rem;
            height: 3.75rem;
            padding: 1.25rem 3.75rem;
            border-radius: 2.5rem;
            font-size: 1.75rem;
            border-width: 0.13rem;
            font-weight: 300;
            line-height: 1.875rem;
            cursor: pointer;
            transition: all ease 0.3s;
            &:hover {
                background: var(--white);
                color: var(--new-orange);
            }
        }
        &__refund {
            margin-top: 0.94rem;
            margin-left: 0;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            text-align: center;
            svg {
                display: block;
                width: 1.25rem;
            }
            span {
                margin-left: 0.5rem;
            }
        }
        &__desc {
            margin-top: 3rem;
        }
    }
    .wrap {
        padding-top: 5rem;
        padding-bottom: 5rem;
        border-radius: 0rem;
    }
}