.body {
    position: relative;
    height: 100%;
    width: 100%;
    max-height: 37.5rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.inner {
    width: 100%;
}

.img {
    position: relative;
    margin-top: auto;
    height: 100%;
    width: 22.1875rem;
    flex-shrink: unset;
    z-index: 10;
    picture {
        display: flex;
        width: 100%;
        height: 100%;
    }
}

.slides {
    padding: 0;
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
}

.slide {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity ease 0.3s;
    &.shown {
        opacity: 1;
    }
}

.text {
    padding: 0;
    height: 14.2rem;
}

.title {
    font-weight: 400;
    font-size: 1.50rem;
    line-height: 1.69rem;
    width: 12.56rem;
    font-weight: 500;
}

.wrap {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.quote {
    color: var(--new-orange);
}

@media screen and ( min-width: 641px) {
    
    .slide {
        &__inner {
            padding-left: 18.75rem;
        }
    }
    .text {
        width: 38.0625rem;
    }
    .img {
        position: relative;
        margin-top: auto;
        height: 100%;
        img {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%);
            object-fit: contain;
            object-position: center;
            height: 100%;
            width: auto;
        }
    }
}