// @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');
.wrap{
	width: 100%;
	display: flex;
	flex-direction: column;
}
.item{
	padding: 1.25rem 0.93rem;
	display: flex;
	flex-direction: column;
	width: 100%;
	background: var(--white);
	border-radius: 1.88rem;
	box-shadow: 0rem 0.13rem 0rem rgba(0, 27, 58, 0.25);
	&__header{
		display: flex;
		align-items: center;
	}
	&__img{
		margin-right: 1.25rem;
		width: 4.00rem;
		height: 4.00rem;
		border-radius: 50%;
		overflow: hidden;
	}
	&__body{
		position: relative;
		width: 100%;
	}
	&__name{
		font-weight: 400;
		color: var(--black);
		font-size: 1.25rem;
		
	}
	&__text{
		margin-top: 1.19rem;
		width: 100%;
		color: var(--black);
		font-weight: 300;
		
		p+p{
			margin-top: 0.8rem;
		}
	}
	&__time{
		position: absolute;
		bottom: 0.05rem;
		right: 0.38rem;
		font-family: 'Roboto', sans-serif;
		color: #A1AAB3;
		font-size: 0.75rem;
	}
	&_inverse{
		flex-direction: row-reverse;
		.item__img{
			margin-left: 1rem;
			margin-right: 0.31rem;
		}
		.item__body{

			border-radius: 0.31rem 0.56rem 0 0.56rem;
		}
		.item__tail{
			left: unset;
			right: -0.35rem;
		}
	}
	&+.item{
		margin-top: 1.56rem;
	}
}
.img{
	margin: 1.56rem auto;
	width: 14.56rem;
}
.order{
	&_1{
		order: 1;
		display: none;
	}
	&_2{
		order: 3;
	}
	&_3{
		order: 2;
	}
	&_4{
		order: 4;
	}
	&_5{
		margin-top: 1.56rem;
		order: 6;
	}
	&_6{
		margin-top: -0.14rem;
		order: 5;
	}
}
@media screen and ( min-width: 641px) {
	.wrap{
		flex-wrap: wrap;
		gap: 1.25rem;
		height: 50.50rem;
	}
	.item{
		margin: 0;
		padding: 1.88rem;
		width: 25.00rem;
		&+.item{
			margin-top: 0;
		}
		&__text{
			font-size: 1rem;
			line-height: 1.25rem;
		}
	}
	.img{
		margin: auto auto 0;
		width: 17.69rem;
	}
	.order{
		&_1{
			order: 1;
			display: flex;
		}
		&_2{
			order: 2;
		}
		&_3{
			order: 3;
		}
		&_4{
			order: 4;
		}
		&_5{
			order: 5;
		}
		&_6{
			margin-top: 1.25rem;
			order: 6;
		}
	}
}