.wrap{
	position: relative;
	width: 100%;
	pointer-events: none;
}
.container{
	position: sticky;
	display: flex;
	align-items: center;
	justify-content: center;
	top: 0;
	left: 0;
	width: 100%;
	min-width: 1px;
	min-height: 1px;
	height: 100vh;
	height: calc(var(--vhd, 1vh)*100 );
}
