.popup {
    position: fixed;
    padding: 1.88rem;
    min-height: 30.06rem;
    display: flex;
    flex-direction: column;
    color: var(--green-light);
    width: 100%;
    left: 0;
    bottom: 0;
    background: var(--almost-white);
    z-index: 10001;
    border-radius: 2.5rem 2.5rem 0 0;
    transform: translate(0%, 150%) translateZ(10px);
    box-shadow: 0rem 1.5rem 1.5rem #DAE1C9;
    transition: transform ease 0.5s;
    &_login{
        justify-content: center;
    }
    &_active {
        transform: translate(0%, 0%) translateZ(10px);
    }
    &__close {
        position: absolute;
        top: 1.5rem;
        right: 1.5rem;
        z-index: 10;
        width: 1rem;
        height: 1rem;
        opacity: 0.5;
        cursor: pointer;
    }
    &__header {
        margin-bottom: 0.25rem;
        display: flex;
        align-items: center;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        span {
            padding: 0.13rem 0.69rem;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-left: 0.5rem;
            background: var(--yellow);
            border-radius: 0.50rem;
        }
    }
    &__title {
        font-size: 2.50rem;
        line-height: 3.00rem;
        // width: 19.25rem;
    }
    &__desc {
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 300;
        width: 100%;
    }
    &__price {
        margin-top: 2.5rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        text-align: center;
    }
    &__btns {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__btn {
        height: 2.5rem;
        width: 10.31rem;
        border-radius: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--main);
        color: var(--white);
        font-size: 1rem;
        cursor: pointer;
        &_disable {
            opacity: 0.2;
            pointer-events: none;
        }
    }
}

.form {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    &_login {
        margin-top: 1.88rem;
    }
    &__label {
        position: relative;
        padding: 0 0.93rem 1.06rem;
        height: 4.38rem;
        border-radius: 2.5rem;
        display: flex;
        flex-direction: column;
        background: var(--white);
    }
    &__desc {
        position: absolute;
        bottom: 1.13rem;
        left: 50%;
        width: 17.81rem;
        height: 0.06rem;
        z-index: 10;
        transform: translateZ(0);
    }
    &__input {
        margin-top: auto;
        width: 100%;
        height: 2.5rem;
        border: none;
        background: none;
        border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        outline: unset;
        font-weight: 300;
        font-size: 1.38rem;
        color: var(--green-light);
        user-select: unset;
        &::placeholder {
            color: var(--green-light);
        }
        &_disable {
            border-bottom: 0.06rem solid var(--green-light);
        }
    }
    &__btn {
        padding-bottom: 0.2rem;
        margin-top: 1.23rem;
        width: 100%;
        height: 3.50rem;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: unset;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        color: var(--white);
        font-size: 1.38rem;
        transition: all ease 0.3s;
        svg {
            width: 4.50rem;
            path {
                fill: var(--white);
                transition: all ease 0.3s;
            }
            &:nth-child(1){
                display: none;
            }
        }
        &_disable {
            background: transparent;
            opacity: 0.2;
            color: var(--black);
            border: 0.13rem solid #BDBDBD;
            pointer-events: none;
            svg path {
                fill: #BDBDBD;
            }
        }
        &_loading {
            position: relative;
            color: transparent;
            pointer-events: none;
            user-select: none;
            &::after {
                content: "";
                position: absolute;
                width: 1.5rem;
                height: 1.5rem;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                border: 0.13rem solid transparent;
                border-top-color: #ffffff;
                border-radius: 50%;
                animation: button-loading-spinner 1s linear infinite;
            }
            svg{
                display: none !important;
            }
        }
    }
}

@keyframes btn-rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
        background: #F7F9A5;
    }
    100% {
        transform: translate(-50%, -50%) rotate(15deg);
        background: #FEF394;
    }
}

@keyframes button-loading-spinner {
    from {
        transform: rotate(0turn);
    }
    to {
        transform: rotate(1turn);
    }
}

@media screen and ( min-width: 641px) {
    .popup {
        padding: 2.69rem 5.00rem;
        width: 77.50rem;
        height: 18.75rem;
        min-height: unset;
        left: 50%;
        bottom: 1rem;
        border-radius: 2.5rem;
        color: var(--green-dark);
        transform: translate(-50%, 150%) translateZ(10px);
        &_active {
            transform: translate(-50%, 0%) translateZ(10px);
        }
        &__desc {
            width: 52rem;
            white-space: pre-wrap;
        }
        &__close {
            position: absolute;
            top: 2rem;
            right: 2rem;
            z-index: 10;
            width: 1.5rem;
            height: 1.5rem;
            opacity: 0.5;
            cursor: pointer;
        }
    }
    .form {
        flex-direction: row;
        &__label {
            padding: 0 1.88rem 1.31rem;
            height: 5.63rem;
            width: 25.00rem;
            border-radius: 1.88rem;
        }
        &__input {
            height: 3.56rem;
            border-bottom: 0.06rem solid var(--green-light);
            font-size: 1.75rem;
            color: var(--green-light);
            &::placeholder {
                color: var(--green-light);
            }
            &_disable {
                border-bottom: 0.06rem solid var(--green-light);
            }
        }
        &__btn {
            padding: 0;
            margin-top: 0;
            margin-left: auto;
            width: 23.75rem;
            height: 5.63rem;
            border-radius: 1.88rem;
            border: 0.13rem solid var(--new-orange);
            cursor: pointer;
            &_disable {
                border: 0.13rem solid #BDBDBD;
            }
            &:nth-child(2) {
                width: 18.38rem;
            }
            &:nth-child(3) {
                width: 21.63rem;
            }
            &_agree {
                margin-top: auto;
                margin-left: 0;
                width: 11.25rem;
            }
            svg {
                &:nth-child(1){
                    display: flex;
                }
                &:nth-child(2){
                    display: none;
                }
            }
            &:hover {
                background: var(--almost-white);
                color: var(--new-orange);
                svg path{
                    fill: var(--new-orange);;
                }
            }
            &_loading {
                &::after {
                    width: 2.5rem;
                    height: 2.5rem;
                    border: 0.24rem solid transparent;
                    border-top-color: #ffffff;
                }
            }
        }
        
        &_login{
            .form__label{
                width: 58.50rem;
            }
            .form__btn{
                width: 7.75rem;
            }
        }
    }
}