.arrow {
    margin: 3.4rem auto;
    width: 1rem;
}

.container {
    position: relative;
    width: 100%;
    padding-left: 0.63rem;
    padding-right: 0.63rem;
}

.cover {
    width: 100%;
    &__wrap {
        width: 100%;
    }
    &__inner {
        position: relative;
        padding-top: 5.43rem;
        display: flex;
        flex-direction: column;
    }
    &__text {
        margin-top: 1.8rem;
        width: 100%;
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 400;
        color: var(--new-orange);
    }
    &__img {
        // padding-left: 4.3rem;
        margin: 2.4rem -0.6rem 0;
        width: 22.93rem;
        overflow: hidden;
        transition: opacity ease 0.3s;
    }
    &__desc {
        margin-top: 2.1rem;
        width: 100%;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 1.6875rem;
        img {
            display: none;
        }
    }
    &__type {
        // margin-top: -1rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        letter-spacing: 0.07em;
        font-weight: 400;
    }
    &__title {
        font-size: 2.625rem;
        font-weight: 400;
        line-height: 2.75rem;
        /* 104.762% */
        letter-spacing: -0.02625rem;
    }
    &__btns {
        display: none;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 3.50rem;
        border-radius: 2.5rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund {
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        width: 100%;
        text-align: center;
        font-size: 1rem;
        line-height: 1.5rem;
        svg {
            display: none;
        }
    }
}

.img {
    display: flex;
    &_1 {
        margin: 3.37rem auto;
        width: 11.25rem;
        height: 11.25rem;
    }
    &_2 {
        margin: 1.47rem auto 0;
        width: 23.38rem;
        height: 13.875rem;
    }
}

.section {
    margin-top: 6rem;
    width: 100%;
}

.stages{
    width: 100%;
}
.typography {
    margin-bottom: 2.2rem;
    font-size: 1.875rem;
    font-weight: 400;
    line-height: 2rem;
}

.quote {
    margin-top: 0.8rem;
    width: 100%;
    font-size: 1.625rem;
    font-weight: 300;
    line-height: 2rem;
    color: var(--new-orange);
    &_2 {
        margin: 1.8rem 0;
    }
}

@media screen and ( min-width: 641px) {
    .arrow {
        display: none;
    }
    .container {
        margin: 0 auto;
        position: relative;
        width: 77.50rem;
        padding: 0;
    }
    .cover {
        position: relative;
        &__inner {
            padding-top: 4.13rem;
        }
        &__title {
            width: 38.8rem;
            font-size: 4.5rem;
            font-weight: 400;
            line-height: 105%;
            letter-spacing: -0.045rem;
        }
        &__img {
            margin: 0;
            position: absolute;
            width: 31.75rem;
            right: 4.4rem;
            top: 0;
            height: 100%;
            overflow: unset;
            img{
                position: sticky;
                top: 6.58rem;
                left: 0;
            }
        }
        &__text {
            width: 38.8rem;
            margin-top: 2.9rem;
            width: 36.69rem;
        }
        &__desc {
            margin-top: 4rem;
            width: 38.8rem;
            font-size: 1.375rem;
            font-style: normal;
            font-weight: 300;
            line-height: 1.6875rem;
        }
        &__btns {
            margin-top: 1.58rem;
            display: flex;
        }
        &__btn {
            height: 4.69rem;
            width: 23.75rem;
            border-radius: 1.88rem;
            cursor: pointer;
            transition: all ease 0.3s;
            &:hover {
                background: #EBF5D9;
                color: var(--new-orange);
            }
        }
        &__refund {
            margin-top: 0;
            margin-left: 1.25rem;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            svg {
                display: block;
                width: 1.25rem;
            }
            span {
                margin-left: 0.5rem;
            }
        }
    }
    .img{
        &_1{
            margin: 3.37rem auto 2.67rem;
        }
        &_2{
            margin: 5.87rem auto 0;
            width: 35.5625rem;
height: 20.8125rem;
        }
    }
    .section {
        margin: 5.63rem auto 0;
        width: 38.0625rem;
        .container{
            width: 100%;
        }
    }
    .stages{
        width: 100%;
        &_1{
            margin-top: 5.7rem;
        }
        &_2{
            margin-top: 5.7rem;
        }
    }
    .quote {
        margin-top: 3.8rem;
        width: 38.8rem;
        font-size: 2.5rem;
        font-weight: 300;
        line-height: 3rem;
        &_2 {
            margin: 1.8rem 0;
            width: 38.0625rem;
            font-size: 1.625rem;
            font-weight: 300;
            line-height: 2rem;
        }
    }
}