.sticky_wrap {
    position: relative;
    width: 100%;
    // height: 500vh;
}

.wrap {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.inner {
    width: 100%;
}

.navs {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, calc(-50% - 5rem));
    width: 22.25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
}

.nav {
    padding: 0.1rem 0;
    width: 2.25rem;
    height: 2.25rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--white);
    color: var(--black);
    font-size: 1.38rem;
    line-height: 1.46rem;
    svg {
        width: 1.06rem;
    }
}

.body {
    padding: 0 0 6.25rem;
    position: relative;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.slides {
    padding: 0 1.25rem;
    position: relative;
    height: 100%;
    width: fit-content;
    display: flex;
}

.slide {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 33.88rem;
    height: 30.19rem;
    &__inner {
        margin-top: 6.69rem;
        padding-top: 4.06rem;
        padding-left: 2.5rem;
        width: 100%;
        height: 19.19rem;
        border-radius: 1.25rem;
        background: #F6FAF5;
    }
    &+.slide {
        margin-left: 2.5rem;
    }
}

.title {
    font-weight: 400;
    font-size: 1.50rem;
    line-height: 1.69rem;
    width: 12.56rem;
    font-weight: 500;
}

.img {
    position: absolute;
    display: flex;
    right: 0;
    top: 0;
    height: 100%;
    width: 18.75rem;
    z-index: 10;
    img {
        height: 100%;
        width: auto;
    }
}

.desc {
    margin-top: 0.25rem;
    width: 12.56rem;
    font-size: 1.25rem;
    line-height: 1.69rem;
}

.pagination {
    margin-top: 1.25rem;
    height: 0.63rem;
}

