@media screen and ( min-width: 641px) {
    .body {
        padding: 0;
    }
    .slides {
        padding: 0 6.25rem;
    }
    .slide {
        &__inner {
            padding-left: 18.75rem;
        }
    }
    .img {
        position: absolute;
        right: 15rem;
        top: 0;
    }
}