.form {
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 20.9375rem;
    &__btn {
        margin-top: 1.5rem;
        display: flex;
        height: 2.5rem;
        padding: 0.875rem 0.625rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 1.25rem;
        background: var(--greeny, #78A671);
        outline: none;
        border: none;
        color: var(--white);
        font-size: 1rem;
        text-align: center;
        font-weight: 300;
        line-height: 1.25rem;
        cursor: pointer;
        transition: background ease 0.3s;
        &.disable {
            background: #B3B3B3;
            pointer-events: none;
        }
    }
    &__checkbox {
        margin-top: 1.5rem;
        cursor: pointer;
        width: 100%;
        display: flex;
        align-items: flex-start;
        gap: 0.5rem;
        align-self: stretch;
        input {
            display: none;
            &:checked~.form__icon {
                background: var(--greeny, #78A671);
                border-color: var(--greeny, #78A671);
                svg {
                    opacity: 1;
                }
            }
        }
        a {
            border-bottom: max(0.06rem, 1px) solid var(--black);
        }
    }
    &__icon {
        position: relative;
        width: 1.125rem;
        height: 1.125rem;
        border-radius: 0.25rem;
        border: 2px solid var(--Checkbox-border, rgba(0, 0, 0, 0.25));
        background: var(--White, #FFF);
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 0.875rem;
            height: 0.875rem;
            opacity: 0;
        }
    }
    &__text {
        font-size: 1rem;
        font-weight: 300;
        line-height: 1.25rem;
        flex-shrink: unset;
    }
    &__label {
        display: flex;
        flex-direction: column;
        width: 100%;
        background: #F8F8F8;
        padding: 1rem 0.75rem;
        border-radius: 0.75rem;
        span {
            font-size: 0.75rem;
            font-weight: 400;
            line-height: 1.25rem;
            letter-spacing: 0.0525rem;
            text-transform: uppercase;
        }
    }
    &__input {
        margin-top: 0.38rem;
        width: 100%;
        height: 1.88rem;
        font-size: 1.375rem;
        font-weight: 300;
        line-height: 1.6875rem;
        background: #F8F8F8;
        outline: none;
        border: none;
        border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
            -webkit-appearance: none;
        }
    }
    input:-internal-autofill-selected, input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:autofill, input:-webkit-autofill-strong-password, input:-webkit-autofill-strong-password-viewable, input:-webkit-autofill-and-obscured {
        background-color: #F8F8F8 !important;
        -webkit-box-shadow: 0 #F8F8F8 inset;
        color: var(--black) !important;
        transition: background-color 5000s ease-in-out 0s;
    }
    &.disable {
        pointer-events: none;
    }
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.popup {
    position: fixed;
    z-index: 10000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    pointer-events: none;
    transition: opacity ease 0.3s;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__body {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 100;
        display: flex;
        width: 20.9375rem;
        height: 13.625rem;
        padding: 1rem 0.75rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        gap: 1rem;
        border-radius: 0.75rem;
        background: var(--grey, #F8F8F8);
        box-shadow: 0rem 0.13rem 1.5rem rgba(0, 0, 0, 0.10);
        font-size: 1.375rem;
        line-height: 1.75rem;
    }
    &__btn {
        display: flex;
        height: 2.5rem;
        padding: 0.875rem 1.875rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border-radius: 1.25rem;
        border: none;
        font: inherit;
        font-size: 1rem;
        color: var(--white);
        cursor: pointer;
        background: var(--greeny, #78A671);
    }
}

.text {
    font-size: 1.375rem;
    font-weight: 300;
    line-height: 1.6875rem;
}

.title {
    margin-top: 3rem;
    color: var(--new-orange, #F9651D);
    font-size: 1.625rem;
    font-weight: 300;
    line-height: 2rem;
    width: 100%;
}

.wrap {
    margin-top: 6rem;
    padding: 0 0.63rem 6rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media screen and ( min-width: 641px) {
    .form {
        margin-top: 3rem;
        display: flex;
        flex-direction: column;
        width: 100%;
        &__label {
            padding: 1.2rem 0.75rem;
        }
        &__btn {
            height: 3.75rem;
            padding: 0.875rem 0.625rem;
            font-size: 1.75rem;
            font-weight: 300;
            line-height: 1.875rem;
        }
    }
    .popup {
        &__body {
            width: 38.0625rem;
            height: 13.625rem;
            gap: 1rem;
            font-size: 1.75rem;
            line-height: 1.875rem;
        }
        &__btn{
            
height: 3.75rem;
padding: 0.875rem 1.875rem;
border-radius: 1.25rem;

font-size: 1.75rem;
        }
    }
    .wrap {
        margin: 9.4rem auto 0;
        padding: 0 0 9rem;
        width: 38.0625rem;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}