.wrap{
	
	width: 100%;
	display: flex;
	flex-direction: column;
}
.card{
	position: relative;
	padding: 2.19rem 1.63rem;
	width: 100%;
	height: 12.94rem;
	color: var(--black);
	background: var(--almost-white);
	border-radius: 1.5rem;
	&+.card{
		margin-top: 5.63rem;
	}
}
.img{
	position: absolute;
	left: 1rem;
	top: -4.06rem;
	width: 6.00rem;
}
.title{
	font-weight: 500;
	font-size: 1.5rem;
	line-height: 1.75rem;
}
.text{
	margin-top: 0.63rem;
	font-size: 1.25rem;
	line-height: 1.69rem;
}
@media screen and ( min-width: 641px) {
	.wrap{
		flex-direction: row;
		justify-content: space-between;
	}
	.card{
		padding: 1.79rem 1.63rem;
		width: 18.44rem;
		height: 14.69rem;
		&+.card{
			margin-top: 0;
		}
		&:last-child{
			.text{
				width: 90%;
			}
		}
	}
	.text{
		width: 98%;
	}
}