.popup{
    position: fixed;
    padding: 1.25rem 0.63rem 0.63rem;
    min-height: 11.25rem;
    width: 22.19rem;
    left: 50%;
    transform: translate(-50%, 150%) translateZ(10px);
    bottom: 0.63rem;
    background: var(--white);
    transition: transform ease 0.5s;
    z-index: 1001;
    border-radius: 1.25rem;
    box-shadow: 0rem 0.13rem 1.56rem rgba(0, 0, 0, 0.1);
    &_active{
        transform: translate(-50%, 0%) translateZ(10px);
    }
    // &_submit{
    //     background: var(--main);
    //     color: var(--white);
    //     .popup__close{
    //         path{
        
    //             stroke: var(--white);
    //         }
    //     }
    // }
    &__close{
        position: absolute;
        top: 1rem;
        right: 1rem;
        z-index: 10;
        width: 0.75rem;
        height: 0.75rem;
        cursor: pointer;
    }
    &__header{
        
        margin-bottom: 0.25rem;
        display: flex;
        align-items: center;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        span{
            padding: 0.13rem 0.69rem;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-left: 0.5rem;
            background: var(--yellow);
            border-radius: 0.50rem;
        }
    }
    &__title{
        font-size: 1.38rem;
        line-height: 1.56rem;
        width: 19.25rem;
    }
    &__desc{
        margin-top: 0.5rem;
        width: 19.38rem;
    }
    &__price{
        margin-top: 3.88rem;
        font-size: 0.75rem;
        line-height: 1.25rem;
        text-transform: uppercase;
        font-weight: 400;
        letter-spacing: 0.07em;
        text-align: center;
    }
    &__btns{
        margin-top: 0.63rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__btn{
        height: 2.5rem;
        width: 10.31rem;
        border-radius: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--main);
        color: var(--white);
        font-weight: 500;
        font-size: 1rem;
        cursor: pointer;
        &_disable{
            opacity: 0.2;
            pointer-events: none;
        }
        
    }
}
.form{
    margin-top: 1.38rem;
    display: flex;
    flex-direction: column;
    &__label {
        padding: 0.75rem 0.75rem 1.25rem;
        height: 5.63rem;
        display: flex;
        flex-direction: column;
        border-radius: 0.75rem;
        background: var(--grey);
    }
    &__desc {
        font-size: 0.75rem;
        letter-spacing: 0.07em;
        text-transform: uppercase;
    }
    &__input {
        margin-top: 0.5rem;
        padding: 0.31rem 0;
        height: 1.88rem;
        border: none;
        background: none;
        border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        outline: unset;
        font-weight: 300;
        user-select: unset;
        &_disable {
            border-bottom: 0.06rem solid rgba(26, 26, 26, 0.33);
        }
    }
    &__btn {
        margin-top: 0.63rem;
        margin-left: auto;
        width: 10.44rem;
        height: 2.50rem;
        display: flex;
        align-items: center;
        justify-content: center;
        outline: unset;
        border-radius: 1.25rem;
        border: 0.06rem solid var(--main);
        background: var(--main);
        
        color: var(--white);
        letter-spacing: 0.07em;
        text-transform: uppercase;
        font-size: 0.75rem;
        transition: all ease 0.3s;
        cursor: pointer;
        &_disable {
            background: transparent;
            opacity: 0.2;
            color: var(--black);
            border: 0.06rem solid rgba(120, 166, 113, 1);
            pointer-events: none;
        }
        &_loading {
            position: relative;
            color: transparent;
            pointer-events: none;
            user-select: none;
            &::after {
                content: "";
                position: absolute;
                width: 1.5rem;
                height: 1.5rem;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                border: 0.13rem solid transparent;
                border-top-color: #ffffff;
                border-radius: 50%;
                animation: button-loading-spinner 1s linear infinite;
            }
        }
    }
       
}