.wrap {
    // padding-top: 10rem;
    min-height: 100vh;
    padding: 1.25rem 0 8.75rem;
    width: 100%;
    background: var(--new-bg);
    color: var(--green-dark);
}
.container {
    position: relative;
    width: 100%;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
}
.mobile{
    display: inline;
}
.header{
    margin-bottom: 3.75rem;
    font-size: 1.25rem;
    color: var(--black);
}
.lead{
    font-size: 1.38rem;
    line-height: 1.75rem;
    color: var(--green-dark);
}
.section{
    font-size: 1rem;
    line-height: 1.25rem;
    color: var(--black);
    a{
        text-decoration: none;
    }
    h1{
        margin-bottom: 2rem;
        font-size: 2.50rem;
        line-height: 3rem;
        width: 102%;
        color: var(--green-dark);
    }
    h2{
        margin-bottom: 0.33rem;
        font-size: 1.38rem;
        line-height: 1.75rem;
        font-weight: 300;
    }
    ul{
        padding-left: 1.5rem;
        list-style: none;
        li{
            position: relative;
            &::before{
                position: absolute;
                content: '';
                top: 0.6em;
                    left: -0.78em;
                width: 0.14rem;
                height: 0.14rem;
                background: var(--black);
                transform: translateZ(0);
            }
        }
    }
    &+.section{
        margin-top: 2.13rem;
    }
    p+p{
        margin-top: 0.36rem;
    }
    p+ul{
        margin-top: 0.4rem;
    }
   ul+p{
        margin-top: 0.4rem;
    }
    &_1{
        font-size: 1.38rem;
        line-height: 1.75rem;
        p+p{
            margin-top: 0.4rem;
        }
        span:not(.mobile){
            margin-top:0.4rem;
            display: flex;
            line-height: 1.69rem;
        }
    }
}
.btn {
    position: relative;
    margin: 4.08rem auto .65rem;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16.25rem;
    height: 13.75rem;
    color: var(--black);
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    z-index: 10;
    text-align: center;
    text-decoration: none;
    &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        background: var(--yellow);
        border-radius: 12.5rem;
        filter: blur(1.8rem);
        animation: btn-rotate 2s ease-in-out 0.3s infinite alternate;
        z-index: -1;
    }
    &__desc{
        position: absolute;
        text-align: center;
        left: 50%;
        transform: translate(-50%);
        bottom: 0.65rem;
        font-weight: 300;
        font-size: 1rem;
        line-height: 1.25rem;
    }
    &_buy{
        padding-bottom: 1.4rem;
        text-transform: unset;
        font-size: 1.38rem;
        line-height: 1.56rem;
        letter-spacing: normal;
        flex-direction: column;
        align-items: center;
        .oldPrice{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -3rem);
            font-size: 1.00rem;
            font-weight: 300;
            &::after{
                width: 2.91rem;
                top: 0.75rem;
                left: 0rem;
            }
        }
    }
}

.footer{
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--green-dark);
    &__img{
        width: 13.13rem;
        order: 0;
    }
    &__title{
        margin-top: 0.63rem;
        width: 100%;
        max-width: 33.25rem;
        text-align: center;
        font-size: 2.50rem;
        line-height: 2.50rem;
        font-weight: 400;
        order: 1;
    }
    &__btns{
        margin-top: 3.13rem;
        flex-direction: column;
        align-items: center;
        width: 20.94rem;
        order: 3;
    }
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 4.69rem;
        border-radius: 1.88rem;
        border: 0.13rem solid var(--new-orange);
        background: var(--new-orange);
        text-decoration: none;
        font-size: 1.75rem;
        color: var(--white);
    }
    &__refund{
        margin-top: 1.25rem;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        text-align: center;
        text-transform: uppercase;
        font-size: 0.75rem;
        line-height: 1.25rem;
        color: var(--green-dark);
        letter-spacing: 0.07em;
        svg{
            margin-right: 0.5rem;
            width: 1.25rem;
        }
    }
    &__desc{
        order: 2;
        margin-top: 2.5rem;
        width: 20.94rem;
        text-align: center;
        color: var(--green-dark);
        max-width: 41.31rem;
        font-size: 1.25rem;
        line-height: 1.69rem;
        a{
            text-decoration: none;
            font-weight: 400;
        }
    }
}
@keyframes btn-rotate {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
        background: #F7F9A5;
    }
    100% {
        transform: translate(-50%, -50%) rotate(15deg);
        background: #FEF394;
    }
}
@keyframes button-loading-spinner {
    from {
        transform: rotate(0turn);
    }
    to {
        transform: rotate(1turn);
    }
}
@media screen and ( min-width: 641px) {
    .wrap{
        padding: 6.25rem 0;
    }
    .btn, .fixedBtn {
        cursor: pointer;
    }
    .mobile{
        display: none;
    }
    .container {
        margin: 0 auto;
        position: relative;
        width: 51.25rem;
        padding: 0;
    }
    .header{
        margin-bottom: 5.05rem;
        font-size: 1.25rem;
        color: var(--black);
    }
    .lead{
        font-size: 1.38rem;
        line-height: 1.75rem;
        color: var(--green-dark);
    }
    .section{
        h1{
            margin-bottom: 2.7rem;
            font-size: 2.50rem;
            line-height: 3rem;
            color: var(--green-dark);
        }
        h2{
            margin-bottom: 0.83rem;
            font-size: 1.38rem;
            line-height: 1.75rem;
            font-weight: 300;
        }
        ul{
            padding-left: 1.5rem;
            list-style: none;
            li{
                position: relative;
                &::before{
                    position: absolute;
                    content: '';
                    width: 0.14rem;
                    height: 0.14rem;
                    background: var(--black);
                    transform: translateZ(0);
                }
            }
        }
        p+p{
            margin-top: 0.65rem;
        }
        &+.section{
            margin-top: 2.3rem;
        }
        &_1{
            p+p{
                margin-top: 0.7rem;
            }
            span:not(.mobile){
                margin-top:0.4rem;
                display: flex;
                line-height: 1.69rem;
            }
        }
    }
    .footer{
        margin-top: 5rem;
        &__btns{
            margin-top: 2.31rem;
            display: flex;
            align-items: center;
            order: 2;
        }
        &__img{
            width: 13.13rem;
        }
        &__title{
            margin-top: 1.93rem;
        }
        &__btn{
            height: 4.69rem;
            width: 23.75rem;
            border-radius: 1.88rem;
            cursor: pointer;
            transition: all ease 0.3s;
            &:hover{
                background: #EBF5D9;
                color: var(--new-orange);
            }
        }
        &__refund{
            margin-top: 0.94rem;
            margin-left: 0;
            font-size: 0.75rem;
            width: auto;
            line-height: 1.25rem;
            letter-spacing: 0.07em;
            text-transform: uppercase;
            text-align: left;
            text-align: center;
            svg{
                display: block;
                width: 1.25rem;
            }
        }
        &__desc{
            margin-top: 3rem;
            order: 3;
            width: 41.31rem;
        }
    }
}