.wrap{
	width: 100%;
}
.item{
	padding: 2.38rem 0;
	display: flex;
	flex-direction: column;
	width: 100%;
	&__header{
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
	}
	&__img{
		width: auto;
		height: 15.00rem;
		img{
			height: 100%;
			width: auto;
		}
	}
	&__title{
		margin-top: 1rem;
		width: 100%;
		font-size: 1.75rem;
		line-height: 2.25rem;
	}
	&__body{
		margin-top: 0.63rem;
		font-size: 1.25rem;
		line-height: 1.69rem;
	}
	&:first-child{
		padding-top: 0;
	}
	&:nth-child(2){
		border-top: 0.06rem solid rgba(94, 131, 88, 0.3);
		border-bottom: 0.06rem solid rgba(94, 131, 88, 0.3);
	}
	&:last-child{
		padding-bottom: 0;
	}
}
@media screen and ( min-width: 641px) {
	.wrap{
		display: flex;
		justify-content: space-between;
	}
	.item{
		padding: 0;
		width: 20.63rem;
		&:first-child{
			.item__body{
				width: 17.4rem;
			}
		}
		&:last-child{
			width: 19.53rem;
			.item__body{
				width: 17.4rem;
			}
		}
		&:nth-child(2){
			padding: 0 4.5rem;
			width: 28.44rem;
			border-top: none;
			border-bottom:none;
			border-left: 0.06rem solid rgba(94, 131, 88, 0.3);
			border-right: 0.06rem solid rgba(94, 131, 88, 0.3);
		}
	}
}